@import './mixins/mixins';
@import './modules/normalize';
@import './variables/var';

.edit{
	display: flex;
	justify-content: center;
	@include width-height(100%,auto);
	cursor: pointer;
	@include padding(5px, 5px,5px ,5px );
	border:none ;
	background:  $background-color_10;
	border-radius: 4px;
    font-size: 15px;
	font-weight: 600;
	line-height: 19.9995px;
	color: $color_8;
}

#demo {
	@include width-height(450px,auto);
	@include margin(0,auto , 0,auto );
	@include positioned(relative,0 ,0 ,0 ,0 );
	color: $color_1;
	border: 2px dashed $background-color_8;
	transition: transform 0.3s;
	display:none;
}
#status {
	width: 100%;
	border: none;
	box-sizing: border-box
}
#review {
	width: 100%;
	font-size: 12px;
	box-sizing: border-box;
	margin-top: 10px;
}
#review a {
	color: #333;
	text-decoration: none
}
#review div {
	width: 100%;
	padding: 8px;
	border: 1px solid #ddd;
	box-sizing: border-box
}
#review img {
	width: 50%
}
#review h4,
#review p {
	margin: 0px;
	// margin-bottom: 10px
}
#review span {
	color: #aaa;
	text-transform: uppercase
}
#review img.loading {
	width: 50px
}
.url_preview{
	@include padding(10px,0,0,0);
}
.emoji-selector {
    background-color: $background-color_2;
    border-radius: 1rem;
    cursor: default;
    display: none;
    @include padding(0, 0, 0, 0);
    border: 1px solid #eee;
    @include positioned(absolute, 135%, 0, 0, 50%);
    @include width-height(325px, 350px);
    z-index: 5;
    overflow: auto;
    transition: padding-left 0.3s, background-color 0.5s; 
}

.emoji-selector ul.emoji-list {
    background-color: $background-color_2;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    @include padding(0, 1rem, 1rem, 1rem);
    border-radius: 0;
    z-index: 5;
}

.emoji-selector ul.emoji-list li {
    @include width-height(35px, 35px);
    border-radius: 0.25rem;
    font-size: 1.5rem;
    margin: 0.2rem; /* Added margin for better spacing */
}

.emoji-selector.active {
    bottom: 110%;
    opacity: 1;
    pointer-events: all;
}

#webCam{
	display: none;
	@include width-height(250px,100px);
}

.uploadOuter {
	text-align: center;
    @include padding(20px,20px ,20px ,20px );
	display: none;
	strong {
		@include padding(0,10px,0,10px);
	}
}

.dragBox {
	@include width-height(250px,100px);
	@include margin(0,auto , 0,auto );
	@include positioned(relative,0 ,0 ,0 ,0 );
	text-align: center;
	font-weight: bold;
	line-height: 95px;
	color: $color_1;
	border: 2px dashed $background-color_8;
	display: inline-block;
	transition: transform 0.3s;
  
	input[type="file"] {
	  @include positioned(absolute,0 , 0, 0,0 );
	  @include width-height(100%,100%);
	  opacity: 0;
	}
	
	&.draging {
	  transform: scale(1.1);
	}
}

#preview {
	text-align: center;
	img {
	    max-width: 30%;
	}
}


nav {
	@include flex-center;
	@include flex-space-between;
	background-color:$background-color_6;
	@include padding(2px, 2%, 2px,2% );
	@include positioned(sticky,0 ,0 ,0 ,0 );
	z-index: 1;
}

.logo {
	@include width-height(40px,40px);
	// @include margin( 0, 45px,0 ,0 );
}

.nav_left,
.nav_right {
	@include flex-center;
}
.nav_center{
    @include flex-space-between;
	align-items: center;
}
.nav_center ul{
	@include width-height(400px,auto);
    @include flex-space-between;
    align-items: center;
}
.nav_center ul li {
	list-style: none;
	display: inline-block;
	
}

.nav_right{
    @include flex-center;
	align-items: center;
}
.nav_right ul li {
	list-style: none;
	display: inline-block;
	@include width-height(40px,40px);
	background-color: $background-color_1;
	border-radius: 70px;
	
}
	
.nav_right ul li svg {
	@include margin(10px,10px ,10px ,10px );

}

.nav_user_icon img {
	@include width-height(40px,40px);
	object-fit: cover;
	border-radius: 50%;
	cursor: pointer;
}

.nav_user_icon {
	@include margin(0, 0, 0,30px );
}

.search_box {
	background: $background-color_1;
	@include width-height(280px,35px);
	border-radius: 25px;
	@include flex-center;
	@include padding(0,15px ,0 ,15px )
}

.search_box img {
	@include width-height(18px,auto);
}

.search_box input {
	@include width-height(100%,auto);
	background-color: $background-color_4;
	@include padding(10px,10px ,10px ,10px );
	outline: none;
	border: none;
}

.online_icon {
	@include positioned(relative,0 ,0 ,0 ,0 );
}

.online_icon::after {
	content: ' ';
	@include width-height(7px,7px);
	background-color: #41db51;
	border-radius: 50%;
	border: 2px solid $color_4;
	@include positioned(absolute,0 ,0 ,0 ,0 );
}

.container {
	@include flex-space-between;
	padding: 13px 5%;
	@include padding(13px, 5%,13px ,5% );
}

.left_sidebar {
	flex-basis: 25%;
	@include positioned(sticky ,90px ,0 ,0 ,0 );
	align-self: flex-start;
}

.right_sidebar {
	background-color: $color_4;
	flex-basis: 25%;
	@include padding(20px,20px ,20px ,20px );
	@include width-height(auto,89vh);
	border-radius: 4px;
	color: $color_2;
	@include positioned(sticky ,70px ,0 ,0 ,0 );
	align-self: flex-start;
}

.main_content {
	flex-basis: 47%;
	
}

.important_link a,
.shortcut_links a {
	text-decoration: none;
	@include flex-center;
	@include margin(0, 0,30px ,0 );
	color: $color_2;
	@include width-height(fit-content,auto);
}

.important_link a img {
	@include width-height(25px,auto);
	@include margin(0,15px ,0 ,0 );
}

.important_link a:last-child {
	color: $color_3;
}

.important_link {
	border-bottom: 1px solid $background-color_8;
}

.shortcut_links a img {
	@include width-height(40px,auto);
	border-radius: 4px;
	@include margin(0,15px ,0 ,0 );
}

.shortcut_links p {
	@include margin(25px,0 ,25px ,0 );
	color: $color_2;
	font-weight: 500;
}

.sider_bar_title {
	@include flex-center;
	@include flex-space-between;
	@include margin( 0, 0,18px ,0 );
}

.right_sidebar h4 {
	font-weight: 600;
	font-size: 16px;
}

.sider_bar_title a {
	text-decoration: none;
	color: $color_3;
	font-size: 12px;
}

.event {
	display: flex;
	font-size: 14px;
	@include margin(0,0 ,20px ,0 );
}

.event .Left_event {
	border-radius: 10px;
	@include width-height(65px,65px);
	margin-right: 15px;
	@include padding(10px,0 ,0 ,0 );
	text-align: center;
	@include positioned(relative,0 ,0 ,0 ,0 );
	overflow: hidden;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.event .Left_event span {
	position: absolute;
    bottom: 0;
    left: 0;
	@include width-height(100%,auto);
	color: $color_4;
	background-color: $color_3;
	font-size: 10px;
	@include padding( 4px, 0,4px ,0 );
}
.event .Right_event p {
	font-size: 12px;
}

.event .Right_event a {
	font-size: 12px;
	text-decoration: none;
	color: $color_3;
}

.advirsment_side_bar {
	@include width-height(100%,auto);
	@include margin(0,0 ,20px ,0 );
	border-radius: 4px;
}

.online_list {
	@include flex-center;
	@include margin(0, 0,20px ,0 );
}

.online_list .online_icon img {
	@include width-height(40px,40px);
	object-fit: cover;
	border-radius: 50%;
}

.online_list .online_icon {
	@include width-height(40px,auto);
	border-radius: 50%;
	@include margin(0,15px ,0 ,0 );
}

.online_list .online_icon::after {
	top: unset;
	bottom: 5px;
}

.story_gallery {
	@include flex-space-between;
	@include margin(0,0 ,20px ,0 );
}

.story {
	flex-basis: 18%;
	@include padding(32%, 0,0 ,0 );
	@include positioned(relative,0 ,0 ,0 ,0 );
	background-image: linear-gradient($background-color_4, rgba(0, 0, 0, 0.5)), url('../../../assets/images/5.jpg');
	background-position: center;
	background-size: cover;
	border-radius: 10px;
}
.story img {
	@include positioned(absolute,0 ,0 ,0 ,0 );
	@include width-height(45px,45px);
	object-fit: cover;
	border-radius: 50%;
	border: 5px solid  $color_3;
}
.story p {
	position: absolute;
	bottom: 10px;
	@include width-height(100%,auto);
	text-align: center;
	color: $color_4;
	font-size: 14px;
}
//implement 4 story using for loop
@for $i from 2 through 5 {
    .story#{$i}{
        background-image: linear-gradient($background-color_4, rgba(0, 0, 0, 0.5)), url('../../../assets/images/#{$i}.jpg');
    }
}

.story1 img {
	top: unset;
	left: 50%;
	bottom: 40px;
	transform: translateX(-50%);
	border: 0;
	@include width-height(35px,auto);
}

.user_profile {
	@include flex-center;
}
.user_profile img {
	@include width-height(45px,45px);
	object-fit: cover;
	border-radius: 50%;
