}
.user_profile small {
	font-size: 12px;
}
.postContent{
	@include padding(10px,0,0,0);
	font-size: 15px;
	font-weight: 400;
	line-height: 19.9995px;
	color:  rgb(5, 5, 5);
}
.post_input_container {
	@include padding(20px,0 , 0,55px );
}
.post_input_container input {
	@include width-height(100%,auto);
	border: 0;
