}
.add_post_link a img {
	@include width-height(20px,auto);
	@include margin(0,10px ,0 ,0 );
}
.post_container {
	@include width-height(98%,auto);
	background-color: $color_4;
	border-radius: 6px;
	@include padding(20px, 20px,20px ,20px );
	color: $color_2;
	@include margin(20px,0 ,20px ,10px);
}
.grid_image_container {
	@include padding(10px,0,10px,0);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 10px;
}

.grid_image_container .post_img {

    @include width-height(200px,200px);
    object-fit: cover;
}

.more_images {
    position: relative;
}

.more_overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
}

/* Modal styles */
.image-modal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.8);
}

.image-modal-content {
    position: relative;
    margin: auto;
    padding: 20px;
    width: 80%;
    max-width: 700px;
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.image-gallery {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.image-gallery img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
}

.close {
    position: absolute;
    top: 10px;
    right: 20px;
    color: black;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
}

/* Grid layout for post images */
.grid_image_container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 10px;
}

.grid_image_container .post_img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.more_images {
    position: relative;
}

.more_overlay {
    position: absolute;
    top: 0;
    right: 0;
