    outline: 0;
	@include margin(-25px,0,0,0);
	@include padding(10px,15px,10px,15px);
    display: inline-block;
    color: $color_6;
    border-radius: 3px;
    cursor: pointer;
}
.profile_info {
    align-items: flex-start;
	@include flex-space-between;
	@include margin(20px,0,0,0);

}

.info_col {
    flex-basis: 33%;
}

.post_col {
    flex-basis: 65%;
	@include margin(-20px,0,0,0);
}

.profile_intro {
    background-color: #fff;
	@include padding(20px,20px,20px,20px);
	@include margin(0,0,20px,0);
    border-radius: 4px;
}

.profile_intro h3 {
    font-weight: 700;
	color: $color_8;
	font-size: 20px;
	line-height: 24px;
}

.intro_text {
    text-align: center;
	@include margin(10px,0,10px,0);
    font-size: 15px;
	font-weight: 400;
	line-height: 19.9995px;
	color: $color_8;
}

.intro_text img {
	@include width-height(15px,auto);
	@include margin(0,0,-3px,0);
}

.profile_intro hr {
    border: 0;
	@include width-height(auto,1px);
    background-color: $background-color_8;
	@include margin(24px,24px,24px,24px);
}

.profile_intro ul li {
    list-style-type: none;
    font-size: 15px;
    @include margin(15px,0,15px,0);
    @include flex-center;
}

.profile_intro ul li img {
	@include width-height(26px,auto);
	@include margin(0,10px,0,0);
}

.title_box {
    @include flex-center;
    @include flex-space-between;
}

.title_box a {
    text-decoration: none;
    color: $color_3;
    font-size: 14px;
}

.photo_box {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 10px;
	@include margin(15px,0,0,0);
}

.photo_box div img {
	@include width-height(100%,100px);
	object-fit: cover;
    cursor: pointer;
}

.profile_intro p {
    font-size: 14px;
}

.freinds_box {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 10px;
	@include margin(15px,0,0,0);
}

.freinds_box div img {
	@include width-height(100%,100px);
	object-fit: cover;
    cursor: pointer;
	@include padding(0,0,20px,0);
}

.freinds_box div {
    position: relative;
}

.freinds_box p {
    position: absolute;
    bottom: 0;
    left: 0;
}

//media Query File
@media (max-width: 932px) {
	.pd_image{
		@include positioned(relative ,-70px,0,0,100px);
		}
    .profile_data{
		@include positioned(relative ,140px,0,0,-40px);
		@include margin(0,0,210px,0);
    }
	.profile_anchor_left{
		@include width-height(100px,auto);
	}
	.profile_anchor_left a{
        @include margin(0,0,0,10px);
	}
	.profile_anchor_right{
		@include width-height(10px,auto);
		@include positioned(absolute,575px,0,0,370px);
	}
	.proflie_detail_right{
		@include width-height(270px,auto);
		@include positioned(absolute ,80px,0,0,90px);
	}
	.anchor1,.anchor2,.anchor3{
		display: none;
	}
	.right_sidebar {
		display: none;
	}

	.left_sidebar {
		display: none;
	}

	nav {
		flex-wrap: wrap;
	}
	.nav_center{
		display: none;
	}
	.search_box {
		@include width-height(45px,45px);
		@include margin(0,0,0,-45px);
}
	.logo {
		width: 110px;
		@include margin(0,20px,0,0);
	}

	.nav_left ul li img {
		@include width-height(20px,auto);
		@include margin(0,8px,0,8px);
	}

	.nav_user_icon img {
		@include width-height(30px,auto);
	}
  
	.nav_user_icon {
		margin-left: 0;
	}
  
	.add_post_link {
		flex-wrap: wrap;
	}
  
	.main_content {
		flex-basis: 100%;
		
	}
  
	.story img {
		width: 30px;
		border-width: 3px;
		bottom: 30px;
	}
  
	.story p {
		font-size: 10px;
	}
  
	.add_post_link a {
		font-size: 11px;
		margin-right: 12px;
	}
  
	.add_post_link a img {
		width: 16px;
		@include margin(0,5px,0,0);
	}
  
	.post_input_container {
		padding-left: 0;
		width: 70%;
	}
	#button_value {
        @include padding(10px,30px,10px,30px);
        @include margin(0,0,-440px,250px);
	  }
	  .profile_container {
		@include padding(20px,5%,20px,5%);

    }

    .profile_detail {
        flex-wrap: wrap;
    }

    .proflie_detail_right {
        text-align: left;
		@include margin(15px,0,0,0);
    }

    .proflie_detail_right button {
        margin-left: 0;
        margin-right: 10px;
		
    }

    .profile_info {
        flex-wrap: wrap;
    }

    .info_col {
        flex-basis: 100%;
    }

    .post_col {
        flex-basis: 100%;
    }
}


